import { EventsEnum } from '../../types';
import { ensureElement } from '../../utils/utils';
import { Component } from '../base/Component';
export class Success extends Component {
    constructor(container, events) {
        super(container);
        this.events = events;
        this.description = ensureElement('.order-success__description', this.container);
        this.byeButton = ensureElement('.order-success__close', this.container);
        this.byeButton.addEventListener('click', () => {
            this.events.emit(EventsEnum.ORDER_SUCCESS);
        });
    }
    // установка общего количества синапсов
    setTotal(value) {
        this.total = value;
        this.setText(this.description, `Списано ${this.total} синапсов`);
    }
}
