import { EventsEnum } from '../types';
import { ensureElement } from '../utils/utils';
import { Component } from './base/Component';
export class Page extends Component {
    constructor(container, events) {
        super(container);
        this.events = events;
        this._wrapper = ensureElement('.page__wrapper');
        this._gallery = ensureElement('.gallery');
        this._basket = ensureElement('.header__basket');
        this._counter = ensureElement('.header__basket-counter');
        this._basket.addEventListener('click', () => {
            this.events.emit(EventsEnum.BASKET_OPEN);
        });
    }
    // Метод для изменения счетчика корзины на главной странице
    set counter(value) {
        this.setText(this._counter, String(value));
    }
    // Метод для отрисовки галлереи на главной странице
    set gallery(items) {
        this._gallery.replaceChildren(...items);
    }
    //Метод для установки блокировки страницы
    set wrapper(value) {
        this.toggleClass(this._wrapper, 'page__wrapper_locked');
    }
}
