import { Api } from './base/Api';
export class LarekApi extends Api {
    // конструктор для работы с API который принимает экземпляр класса для работы с API
    constructor(baseUrl, cdnUrl, options) {
        super(baseUrl, cdnUrl, options);
    }
    // запрос карточки с сервера по id
    getCard(id) {
        return this.get(`/product/${id}`).then((item) => (Object.assign(Object.assign({}, item), { image: this.cdnUrl + item.image })));
    }
    // запрос массива карточек с сервера
    getCards() {
        return this.get(`/product`).then((data) => data.items.map((item) => (Object.assign(Object.assign({}, item), { image: this.cdnUrl + item.image }))));
    }
    // отправка данных о заказе вводимых пользователем
    postOrderData(order) {
        return this.post('/order', order).then((data) => data);
    }
}
